<template>
  <div class="wrapper">
    <div class="list-page">
      <div class="container">
        <div class="list-title">
          <div class="left">
            <div class="title">交易</div>
            <div class="total">
              总共
              <span>{{allpage}}</span>条交易
            </div>
          </div>
        </div>
        <div class="tableWrapper">
          <div class="tableminwrapper">
            <el-table :data="tableData" style="width: 100%">
              <el-table-column label="ID" width="180">
                <template scope="scoped">
                  <span @click="todetails(scoped.row)" style="cursor:pointer">
                    {{`${scoped.row.id.substring(0, 6)}...${scoped.row.id.substring(
                    scoped.row.id.length - 6
                    )}`}}
                  </span>
                </template>
              </el-table-column>
              <el-table-column prop="timestamp.human" label="时间" width="200"></el-table-column>
              <el-table-column label="付款地址">
                <template scope="scoped">
                  <span @click="towalletdetails(scoped.row.sender)" style="cursor:pointer">
                    {{`${scoped.row.sender.substring(
                    0,
                    6
                    )}...${scoped.row.sender.substring(scoped.row.sender.length - 6)}`}}
                  </span>
                </template>
              </el-table-column>
              <el-table-column label="收款地址">
                <template scope="scoped">
                  <span
                    style="cursor:pointer"
                    v-show="scoped.row.type!==6"
                    @click="towalletdetails(scoped.row.recipient)"
                  >
                    {{`${scoped.row.recipient.substring(
                    0,
                    6
                    )}...${scoped.row.recipient.substring(scoped.row.recipient.length - 6)}`}}
                  </span>
                  <span
                    style="cursor:pointer"
                    v-show="scoped.row.type==6"
                    @click="openmintable(scoped.row)"
                  >Multipayment</span>
                  <!-- <span
                    style="cursor:pointer"
                    v-show="scoped.row.type==6&&copyUrl!==scoped.row.sender"
                    @click="towalletdetails(scoped.row.recipient)"
                  >
                    {{`${copyUrl.substring(
                    0,
                    6
                    )}...${copyUrl.substring(copyUrl.length - 6)}`}}
                  </span>-->
                </template>
              </el-table-column>
              <el-table-column label="交易额">
                <template scope="scoped">{{ (scoped.row.amount/100000000).toFixed(2)}}{{unit}}</template>
              </el-table-column>
              <el-table-column label="手续费">
                <template scope="scoped">{{(scoped.row.fee/100000000).toFixed(2)}}{{unit}}</template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div class="changepage-wrapper">
          <el-pagination
            @current-change="pagechange"
            :pager-count="5"
            :total="allpage"
            :page-size="25"
            layout="prev, pager, next"
          ></el-pagination>
        </div>
      </div>
    </div>
    <el-dialog width="80%" title="Multipayment" :visible.sync="dialogTableVisible">
      <el-table :data="gridData">
        <el-table-column property="recipientId" label="接收地址" width="350">
          <template scope="scoped">
            <span
              style="cursor:pointer"
              @click="towalletdetails(scoped.row.recipientId)"
            >{{scoped.row.recipientId}}</span>
          </template>
        </el-table-column>
        <el-table-column label="交易额" width="200">
          <template scope="scoped">{{(scoped.row.amount/100000000).toFixed(2)}}{{unit}}</template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import moment from "moment/moment";
import { getdealall } from "../../../api";
export default {
  mounted() {
    this.unit = sessionStorage.getItem("unit")
      ? sessionStorage.getItem("unit")
      : "MCN";
    getdealall({ orderBy: "timestamp:desc", page: 1, limit: 25 })
      .then((res) => {
        console.log(res);
        this.allpage = res.data.meta.totalCount;
        for (let v of res.data.data) {
          v.timestamp.human = moment(v.timestamp.human).format(
            "YYYY-MM-DD HH:mm:ss"
          );
          // v.idMin = ;
          if (v.type == 6) {
            for (let r of v.asset.payments) {
              v.amount = v.amount * 1 + r.amount * 1;
            }
          }
        }

        this.tableData = res.data.data;
      })
      .catch((err) => {
        window.console.log(err);
      });
  },
  data() {
    return {
      tableData: [],
      allpage: 0,
      unit: "",
      dialogTableVisible: false,
      gridData: [],
    };
  },
  methods: {
    todetails(id) {
      this.$router.push({
        path: "/tradedetails",
        query: {
          id: id.id,
        },
      });
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    towalletdetails(address) {
      this.$router.push({
        path: "/walletdetails",
        query: {
          id: address,
        },
      });
      // this.$router.go(0);
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    pagechange(e) {
      getdealall({ orderBy: "timestamp:desc", page: e, limit: 25 })
        .then((res) => {
          this.allpage = res.data.meta.totalCount;
          for (let v of res.data.data) {
            v.timestamp.human = moment(v.timestamp.human).format(
              "YYYY-MM-DD HH:mm:ss"
            );
            // v.idMin = ;
            if (v.type == 6) {
              for (let r of v.asset.payments) {
                v.amount = v.amount * 1 + r.amount * 1;
              }
            }
          }

          this.tableData = res.data.data;
        })
        .catch((err) => {
          window.console.log(err);
        });
    },
    openmintable(r) {
      this.dialogTableVisible = true;
      console.log(r.asset.payments);
      this.gridData = r.asset.payments;
    },
  },
};
</script>

